import window from 'global/window';

export default class BaseComponent {
  static abstractMethods = ['init', 'destroy'];
  static defaultProps = {};

  constructor(element, props = {}) {
    if (!(element instanceof window.HTMLElement)) {
      throw new Error('element must be instance of HTMLElement');
    }

    BaseComponent.abstractMethods.forEach((method) => {
      if (!this[method]) {
        throw new Error(`class must implement '${method}' method`);
      }
    });
    this.element = element;
    this.nestedElements = {};
    this.props = {
      ...this.constructor.defaultProps,
      ...props,
    };
  }

  getElement(selectorKey) {
    const selector = this.props.elementSelectors[selectorKey];

    if (!this.nestedElements[selectorKey]) {
      this.nestedElements[selectorKey] = this.element.querySelector(selector);
    }
    return this.nestedElements[selectorKey];
  }

  getElementList(selectorKey) {
    const selector = this.props.elementSelectors[selectorKey];

    if (!this.nestedElements[selectorKey]) {
      this.nestedElements[selectorKey] = [].slice.call(this.element.querySelectorAll(selector));
    }
    return this.nestedElements[selectorKey];
  }
}



