import BaseComponent from '~global-js/component/base-component';
import axios from '~global-js/helpers/axios';
import {
    validateNotEmpty,
    validateEmail,
    validateTelephone,
    validateInternationalTelephone,
    validateYear,
    validateDay,
} from '~global-js/helpers/validator';
import errorCodes from '~global/variables/form-errors.json';

export default class FormValidation extends BaseComponent {
  constructor(element, props) {
    super(element, props);

    if (element.nodeName !== 'FORM') {
      throw new Error('You must specify a form element');
    }

    this.form = element;
    this.fields = element.querySelectorAll('[data-validate]');
    this.actionUrl = element.getAttribute('action') || null;
    this.fieldStates = {};
  }

  init() {
    this.initFieldStates();
    this.addEventListener();
  }

  initFieldStates() {
    for (const field of this.fields) {
      this.fieldStates[field.name] = {
        validation: JSON.parse(field.getAttribute('data-validate')),
        validValue: null,
        changed: false,
        lastError: false,
      };
      if (!field.required || field.value) {
        this.fieldStates[field.name].validValue = field.value;
      }
    }
  }

  addEventListener = () => {
    for (const field of this.fields) {
      field.addEventListener('change', this.changeValidate);
      field.addEventListener('blur', this.blurValidate);
    }

    this.form.addEventListener('submit', this.submitValidate);
  };

  changeValidate = (e) => {
    this.fieldStates[e.target.name].changed = true;
  }

  blurValidate = (e) => {
    const state = this.fieldStates[e.target.name];
    if (state.changed || state.validValue === null) {
      const valid = this.validate(e.target);

      if (valid && this.props.async) {
        this.postData(e.target);
      }
    }
    this.fieldStates[e.target.name].changed = false;
  };

  submitValidate = (e) => {
    e.preventDefault();
    let valid = true;

    for (const field of this.fields) {
      if (!this.validate(field)) {
        valid = false;
      }
    }

    if (this.props.submitCheckCallback) {
      this.props.submitCheckCallback(valid);
    } else if (valid) {
      this.form.submit();
    }
  };

  validate = (element) => {
    const value = element.value;
    const validation = this.fieldStates[element.name].validation;
    validation.required = element.required;
    validation.type = validation.type || element.type;

    if (validation.required && !validateNotEmpty(value)) {
      return this.validationFailed(element, validation, 'blank');
    }

    if (validation.type) {
      switch (validation.type) {
        case 'email':
          if (!validateEmail(value)) {
            return this.validationFailed(element, validation);
          }
          break;

        case 'phone':
          if (!validateTelephone(value)) {
            return this.validationFailed(element, validation);
          }
          break;

        case 'internationalPhone':
          if (!validateInternationalTelephone(value)) {
            return this.validationFailed(element, validation);
          }
          break;

        case 'date': {
          const dayElement = document.getElementById(validation.dateDetail.day);
          const yearElement = document.getElementById(validation.dateDetail.year);
          const monthElement = document.getElementById(validation.dateDetail.month);
          const yearValidation = this.fieldStates[yearElement.name].validation;

          if (yearElement.value !== '' && !validateYear(
              yearElement.value,
              yearValidation.yearOffsetStart,
              yearValidation.yearOffsetEnd)
          ) {
            return this.validationFailed(yearElement, yearValidation);
          } else if (yearElement.value !== '') {
            this.validationPassed(yearElement, yearValidation, false);
          }

          if (dayElement.value !== '' && !validateDay(dayElement.value, monthElement.value, yearElement.value)) {
            return this.validationFailed(dayElement, validation);
          } else if (dayElement.value !== '') {
            this.validationPassed(dayElement, validation, false);
          }

          break;
        }

        case 'regex': {
          const patternInfo = validation.pattern.match(/^\/(.*?)\/([gimy])*$/);
          const expr = new RegExp(patternInfo[1], patternInfo[2]);
          if (!expr.test(value)) {
            return this.validationFailed(element, validation);
          }
          break;
        }

        default:
          break;
      }
    }

    // minLength/maxLength default value of element is -1
    const minLength = validation.minLength || element.minLength;
    if (minLength && minLength !== -1 && value.length < minLength) {
      return this.validationFailed(element, validation);
    }
    const maxLength = validation.maxLength || element.maxLength;
    if (maxLength && maxLength !== -1 && value.length > maxLength) {
      return this.validationFailed(element, validation);
    }

    return this.validationPassed(element, validation);
  };

  validationFailed = (element, validation, type = 'invalid', emitEvent = true) => {
    const fieldContainer = element.closest(`.${this.props.baseClass}`);
    const errorMessageBaseContainer = validation.errorBaseClass
      ? element.closest(`.${validation.errorBaseClass}`)
      : fieldContainer;

    if (validation.enableErrMessage === false) {
      fieldContainer.classList.remove(`${this.props.baseClass}--valid`).add(`${this.props.baseClass}--error`);
      return false;
    }

    const errorMessageElement = this.getErrorMessageElement(errorMessageBaseContainer);
    let errorCode = validation.errors.default;

    if (type === 'invalid' && validation.errors.invalid) {
      errorCode = validation.errors.invalid;
    } else if (type === 'blank' && validation.errors.blank) {
      errorCode = validation.errors.blank;
    } else if (typeof type === 'number') {
      errorCode = type;
    } else if (validation.errors.default) {
      errorCode = validation.errors.default;
    }

    errorMessageElement.innerHTML = this.props.i18n.t(`${errorCodes[errorCode].key}`);
    fieldContainer.classList.remove(`${this.props.baseClass}--valid`);
    fieldContainer.classList.add(`${this.props.baseClass}--error`);

    // Save error
    this.fieldStates[element.name].lastError = type;
    if (emitEvent && !this.props.async && this.props.validationCallback) {
      this.props.validationCallback(element, false);
    }
    return false;
  };

  getErrorMessageElement = (errorMessageBaseContainer) => {
    let errorMessageElement = null;

    if (errorMessageBaseContainer.querySelector(`.${this.props.baseClass}__error-message`)) {
      errorMessageElement = errorMessageBaseContainer.querySelector(`.${this.props.baseClass}__error-message`);
    } else {
      errorMessageElement = document.createElement('p');
      errorMessageElement.classList.add(`${this.props.baseClass}__error-message`);
      errorMessageBaseContainer.appendChild(errorMessageElement);
    }

    return errorMessageElement;
  };

  validationPassed = (element, validation, emitEvent = true) => {
    const fieldContainer = element.closest(`.${this.props.baseClass}`);
    const errorMessageBaseContainer = validation.errorBaseClass
      ? element.closest(`.${validation.errorBaseClass}`)
      : fieldContainer;

    const errorElement = errorMessageBaseContainer.querySelector(`.${this.props.baseClass}__error-message`);
    if (errorElement) {
      errorElement.parentNode.removeChild(errorElement);
    }

    fieldContainer.classList.remove(`${this.props.baseClass}--error`);
    fieldContainer.classList.add(`${this.props.baseClass}--valid`);

    // Show other message if base container still has error field
    const errorFields = errorMessageBaseContainer.querySelectorAll(`.${this.props.baseClass}--error [data-validate]`);
    if (errorFields.length > 0) {
      const errorState = this.fieldStates[errorFields[0].name];
      this.validationFailed(errorFields[0], errorState.validation, errorState.lastError, false);
    }

    const state = this.fieldStates[element.name];
    state.lastError = null;
    state.validValue = element.value;
    if (emitEvent && !this.props.async && this.props.validationCallback) {
      this.props.validationCallback(element, true);
    }
    return true;
  };

  postData = (element) => {
    axios.post(this.actionUrl, {
      [element.getAttribute('name')]: element.value,
    }).then((response) => {
      let valid;
      if (response.data.status !== 'ok' && response.data.error) {
        this.validationFailed(
          element,
          JSON.parse(element.getAttribute('data-validate')),
          response.data.error || 'invalid'
        );
        valid = false;
      } else {
        this.fieldStates[element.name].validValue = element.value;
        valid = true;
      }

      if (this.props.validationCallback) {
        this.props.validationCallback(element, valid);
      }

      if (this.props.asyncCallback) {
        this.props.asyncCallback(element, response.data);
      }
    });
  };

  /**
   * A method to get all valid values.
   *
   * @returns {{Object}}
   */
  getAllValidValues() {
    const values = {};
    for (const fieldName of Object.keys(this.fieldStates)) {
      const validValue = this.fieldStates[fieldName].validValue;
      if (validValue !== null) {
        values[fieldName] = validValue;
      }
    }
    return values;
  }

  destroy() {
    for (const field of this.fields) {
      field.removeEventListener('blur', this.blurValidate);
      field.removeEventListener('change', this.changeValidate);
    }

    this.form.removeEventListener('submit', this.submitValidate);
  }

  static defaultProps = {
    baseClass: 'field',
    async: false,
    asyncCallback: null,
    submitCheckCallback: null,
    validationCallback: null,
  }
}



